import React, { useState } from 'react';
import classNames from 'classnames';
import NumberContainer from './numberContainer';

const Calculator = () => {
    const initialValue = {
        randomNum1 : Math.floor(Math.random() * (30 - 20)) + 20,
        randomNum2 : Math.floor(Math.random() * (20 - 1)) + 1,
        score : parseInt(0),
        checkValid : '',

    }
    const [num1 , setNum1] = useState(initialValue.randomNum1);
    const [num2 , setNum2] = useState(initialValue.randomNum2);
    const [score, setScore] = useState(initialValue.score);
    const [value, setValue] = useState('');
    const [checkValid, setCheckValid] = useState(initialValue.checkValid);

    const handleChange = event => {
        setValue(event.target.value);
        setCheckValid(initialValue.checkValid);
    }

    const randomise = () => {
        setNum1(initialValue.randomNum1);
        setNum2(initialValue.randomNum2);
        setValue('');
    }

    const handleSubmit = event =>{
        if (parseInt(value) === parseInt(num1 + num2)) {
            //Correct
            setScore(score + 1);
            setCheckValid(initialValue.checkValid);
            randomise();
            setCheckValid(classNames({ 'tada animated fast': true }));
        } else {
            //Wrong
            setCheckValid(classNames({ 'shake animated fast': true }));
        }
        event.preventDefault();
    }

    const reset = () => {
        randomise();
        setScore(initialValue.score);
    }

    // if (score > 2) {
    //     return (
    //         <h1 className="text-center">Yay</h1>
    //     )
    // }
    // else {
        return (
            <React.Fragment>
                <div className={`alert alert-primary ${checkValid}`}>
                    <p className="text-center">Score</p>
                    <h1 className="text-center">{score}</h1>
                </div>
                <div className="questionContainer">
                    <div className="container">
                        <div className="row">
                            <div className="col d-flex align-items-end">
                                <h1>+</h1>
                            </div>
                            <div className="col">
                                <NumberContainer num={num1}/>
                                <NumberContainer num={num2}/>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="form-row text-right">
                    <form action="" autoComplete="off" onSubmit={event => {handleSubmit(event)}}>
                        <input name="result" className="text-right form-control" type="number" value={value} onChange={event => {handleChange(event)}} autoComplete="off" />
                        <button className="btn btn-primary btn-block mt-2" type="button" onClick={event => {handleSubmit(event)}} disabled={!value}>Submit</button>
                        
                    </form>
                    <button className="btn btn-link btn-block mt-4" type="button" onClick={() => {reset()}}>Reset</button>
                </div>
            </React.Fragment>
        )
    // }
}

export default Calculator;